
import 'regenerator-runtime/runtime';
const BASE_URL = '/api/users';

// Универсальная функция для выполнения fetch запросов
async function apiFetch(endpoint,data) {
    const url = `${BASE_URL}${endpoint}`;
    try {
        const response = await fetch(url,data);

        if (!response.ok) {
            throw new Error(`Ошибка: ${response.status} ${response.statusText}`);
        }

        return await response.json();
    } catch (error) {
        console.error('Ошибка при выполнении запроса:', error);
        throw error; // Пробрасываем ошибку дальше, чтобы обработать её на уровне компонента
    }
}

// Функция для GET запросов
export async function getData(endpoint) {
    return apiFetch(endpoint);
}

// Функция для POST запросов
export async function postData(endpoint, data) {
    return apiFetch(endpoint, {
        method: 'POST',
        body: JSON.stringify(data)
    });
}

// Функция для POST запросов с файлами (например, изображениями)
export async function crAnketPhoto(endpoint, formData) {
    return apiFetch(endpoint, {
        method: 'POST',
        body: formData
    });
}
